        lastName: "",
      },
      passwordType: "password",
      remember: false,
      emailSent: false,
      showRecendLink: false,
      userEmailLink: "",
      hideRecendLink: false,
    };
  },
  computed: {
    isLoginPage() {
      return this.$route.name === "SignIn";
    },
    typeEntry() {
      return this.isLoginPage ? "Sign In" : "Sign Up";
    },
  },
  methods: {
    getIdToken() {
      this.setLoadingStatus(true);
      firebase
        .auth()
        .currentUser.getIdToken() // forceRefresh
        .then((response) => {
          this.$cookies.set("userEventAppToken", response, "5d");
          this.loadCurrentUser()
            .then((response) => {
              if (response.data.emailConfirmed) {
                this.$router.push({ name: "Groups" });
              } else {
                this.emailSent = true;

                this.showRecendLink = true;
                firebase
                  .auth()
                  .signOut()
                  .then(() => {
                    Vue.$cookies.remove("userEventAppToken");
                  });
              }

              this.setLoadingStatus(false);
            })
            .catch((error) => {
              console.log("error", error.response.status);
              if (error.response.status === 403) {
                this.emailSent = true;
              }
              firebase
                .auth()
                .signOut()
                .then(() => {
                  Vue.$cookies.remove("userEventAppToken");
                });
            });
        })
        .catch((error) => {
          console.log("error", error);
        });
    },
    signHandler() {
      if (this.isLoginPage) {
        this.signIn();
      } else {
        this.signUp();
      }
    },
    signUp() {
      console.log(this.form);
      this.setLoadingStatus(true);
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.form.email, this.form.pass)
        .then(() => {
          let user = firebase.auth().currentUser;
          if (user) {
            let tmp = {
              email: this.form.email,
            };
            this.makeRequest("CREATE_USER", "POST", tmp)
              .then((response) => {
                const verifyURL = response.data.verifyUrl;

                setTimeout(() => {
                  let actionCodeSettings = {
                    url: verifyURL,
                    handleCodeInApp: true,
                  };
                  firebase
                    .auth()
                    .sendSignInLinkToEmail(tmp.email, actionCodeSettings)
                    .then(() => {
                      this.emailSent = true;
                      firebase
                        .auth()
                        .signOut()
                        .then(() => {
                          Vue.$cookies.remove("userEventAppToken");
                        });
                      window.localStorage.setItem("emailForSignIn", tmp.email);
                      this.setLoadingStatus(false);
                    })
                    .catch((error) => {
                      console.log(error);
                      this.setLoadingStatus(false);
                    });
                }, 1000);
              })
              .catch(() => {
                this.setLoadingStatus(false);
              });
          } else {
            this.$notify({
              group: "system",
              title: "Oops!",
              text: "No user defined!",
              type: "error",
            });
            this.setLoadingStatus(false);
          }
        })
        .catch((error) => {
          this.$notify({
            group: "system",
            title: "Oops!",
            text: "error: " + error,
            type: "error",
          });
          this.setLoadingStatus(false);
        });
    },
    signIn() {
      this.setLoadingStatus(true);
      firebase
        .auth()
        .signInWithEmailAndPassword(this.form.email, this.form.pass)
        .then(() => {
          let user = firebase.auth().currentUser;
          if (user) {
            this.getIdToken();
          } else {
            this.$notify({
              group: "system",
              title: "Oops!",
              text: "No user defined!",
              type: "error",
            });
          }
          this.setLoadingStatus(false);
        })
        .catch((error) => {
          this.$notify({
            group: "system",
            title: "Oops!",
            text: "error: " + error,
            type: "error",
          });
          this.setLoadingStatus(false);
        });
    },
    resendEmailLink(hideAfterSubmit = false) {
      let tmp = {
        email: this.form.email,
      };

      this.makeRequest("CREATE_USER", "POST", tmp)
        .then((response) => {
          const verifyURL = response.data.verifyUrl;

          setTimeout(() => {
            let actionCodeSettings = {
              url: verifyURL,
              handleCodeInApp: true,
            };
            firebase
              .auth()
              .sendSignInLinkToEmail(tmp.email, actionCodeSettings)
              .then(() => {
                this.emailSent = true;
                window.localStorage.setItem("emailForSignIn", tmp.email);

                if (hideAfterSubmit) {
                  this.hideRecendLink = true;
                  this.$notify({
                    group: "system",
                    title: "Awesome!",
                    text: "Activation link re-sent!",
                    type: "success",
                  });
                }

                firebase
                  .auth()
                  .signOut()
                  .then(() => {
                    Vue.$cookies.remove("userEventAppToken");
                  });

                this.setLoadingStatus(false);
              })
              .catch((error) => {
                console.log(error);
                this.setLoadingStatus(false);
              });
          }, 1000);
        })
        .catch(() => {
          this.setLoadingStatus(false);
        });
    },
    facebookHandler() {
      const providerFB = new firebase.auth.FacebookAuthProvider();
      firebase
        .auth()
        .signInWithPopup(providerFB)
        .then(() => {
          this.getIdToken();
        })
        .catch((error) => console.log("errorCode", error));
    },
    googleHandler() {
      const provider = new firebase.auth.GoogleAuthProvider();
      provider.addScope("profile");
      provider.addScope("email");

      firebase
        .auth()
        .signInWithPopup(provider)
        .then(() => {
          this.getIdToken();
        })
        .catch((error) => console.log("errorCode", error));
    },
  },
  beforeCreate() {
    if (this.$cookies.get("userEventAppToken")) {
      this.$router.push({ name: "Main" });
    }
  },
};
</script>

